.main-sidenav ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  list-style-type: none;
  padding: 0;
}

.main-sidenav li a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: White;
